import React, {useState, useEffect, useContext} from "react";
import {Link} from "react-router-dom";
import {
	User,
	Settings,
	Bell,
	Menu,
	ChevronDown,
	LogOut,
	AlertTriangle,
} from "lucide-react";
import {AuthContext} from "../../contexts/AuthContext";
import {useHeaderCounters, useInvalidateCounters} from "../../hooks/useHeaderCounters";
import ThemeToggle from "../common/ThemeToggle";
import {useDashboard} from "./DashboardContext";
import NotificationModal from "./NotificationModal";
import {useNotifications} from "../../hooks/useNotifications";

/**
 * Tipos de notificaciones para mostrar en el panel
 */
export interface Notification {
	id: string;
	title: string;
	description: string;
	time: string;
	read: boolean;
	type?: "default" | "warning" | "error" | "success";
}

/**
 * Tipos de acciones pendientes para mostrar en la parte superior
 */
export interface PendingActions {
	[key: string]: number;
}

interface DashboardHeaderProps {
	toggleSidebar: () => void;
	isAdmin?: boolean;
	unreadNotifications?: number;
	notifications?: Notification[];
	pendingActions?: PendingActions;
	onReadAllNotifications?: () => void;
	visibleInMobile?: boolean;
}

/**
 * Componente Header reutilizable para dashboards OPTIMIZADO
 */
const DashboardHeader: React.FC<DashboardHeaderProps> = ({
	toggleSidebar,
	isAdmin = false,
	unreadNotifications: _unreadNotifications,
	notifications: _notifications = [],
	pendingActions = {},
	onReadAllNotifications = () => {},
	visibleInMobile = true,
}) => {
	const [isProfileMenuOpen, setIsProfileMenuOpen] = useState(false);
	const [isNotificationModalOpen, setIsNotificationModalOpen] = useState(false);
	const {user, logout} = useContext(AuthContext);
	const {currentPageTitle} = useDashboard();

	// ‚úÖ USAR EL HOOK UNIFICADO OPTIMIZADO - CORRECCI√ìN PRINCIPAL
	const {counters, loading: countersLoading} = useHeaderCounters();
	const {forceRefresh} = useInvalidateCounters();
	const {markAllAsRead} = useNotifications();

	// ‚úÖ USAR EL CONTADOR DE NOTIFICACIONES DEL HOOK OPTIMIZADO
	// Ya no usar props, solo del hook que se conecta al backend
	const finalUnreadNotifications = counters.notificationCount;

	console.log("DashboardHeader: Contadores optimizados", {
		finalUnreadNotifications,
		hookUnread: counters.notificationCount,
		isAdmin,
		loading: countersLoading,
	});

	// Calcular total de acciones pendientes
	const totalPendingActions = Object.values(pendingActions).reduce(
		(total, value) => total + (value || 0),
		0
	);

	const toggleProfileMenu = () => {
		setIsProfileMenuOpen(!isProfileMenuOpen);
	};

	const toggleNotificationModal = () => {
		setIsNotificationModalOpen(!isNotificationModalOpen);
	};

	// Funci√≥n mejorada para marcar todas las notificaciones como le√≠das
	const handleMarkAllAsRead = async () => {
		const success = await markAllAsRead();
		if (success) {
			// üî• TIEMPO REAL: Refrescar contadores del header
			await forceRefresh();
			console.log("üîî Header - Contadores actualizados despu√©s de marcar todas");
		}
		// Tambi√©n ejecutar callback del prop si existe
		onReadAllNotifications();
	};

	// Obtener la inicial del nombre del usuario para el avatar
	const getInitial = () => {
		return user?.name?.charAt(0).toUpperCase() || (isAdmin ? "A" : "U");
	};

	// Cerrar men√∫s al hacer clic fuera
	useEffect(() => {
		const handleClickOutside = (event: MouseEvent) => {
			const target = event.target as Element;

			if (
				!target.closest(".profile-menu") &&
				!target.closest(".profile-button")
			) {
				setIsProfileMenuOpen(false);
			}
		};

		document.addEventListener("mousedown", handleClickOutside);
		return () => {
			document.removeEventListener("mousedown", handleClickOutside);
		};
	}, []);

	// Obtener el color del avatar basado en el rol
	const getAvatarColor = () => {
		return isAdmin
			? "bg-red-100 text-red-600"
			: "bg-primary-100 text-primary-600";
	};

	return (
		<header className="bg-white shadow-sm z-20">
			<div className="flex items-center justify-between px-4 py-3">
				<div className="flex items-center">
					{visibleInMobile && (
						<button
							className="md:hidden text-gray-600 focus:outline-none mr-3"
							onClick={toggleSidebar}
						>
							<Menu size={24} />
						</button>
					)}

					{/* Indicador de acciones pendientes */}
					{totalPendingActions > 0 && (
						<div className="flex items-center mr-4">
							<span className="bg-red-500 text-white text-sm font-medium rounded-md px-2 py-1 flex items-center">
								<AlertTriangle size={16} className="mr-1" />
								{totalPendingActions} acci√≥n
								{totalPendingActions === 1 ? "" : "es"} pendiente
								{totalPendingActions === 1 ? "" : "s"}
							</span>
						</div>
					)}

					<h1 className="text-lg font-medium text-gray-800 items-center">
						{currentPageTitle}
					</h1>
				</div>

				<div className="flex items-center space-x-4">
					{/* Theme toggle */}
					<ThemeToggle />

					{/* Visit store / Ir a Tienda - Para administradores y sellers */}
					{(isAdmin || user?.role === 'seller') && (
						<a
							href="/"
							target=""
							rel="noopener noreferrer"
							className="text-gray-600 hover:text-gray-900"
						>
							<span className="text-sm">{isAdmin ? 'Visitar Tienda' : 'Ir a Tienda'}</span>
						</a>
					)}

					{/* üéÜ NOTIFICACIONES CON MODAL Y BOT√ìN DIRECTO */}
					<div className="flex items-center space-x-2">
						{/* Bot√≥n directo marcar todas como le√≠das */}
						{finalUnreadNotifications > 0 && (
							<button
								onClick={handleMarkAllAsRead}
								className="text-xs text-primary-600 hover:text-primary-700 px-2 py-1 rounded-md hover:bg-primary-50 transition-colors"
								title="Marcar todas como le√≠das"
							>
								Marcar todas
							</button>
						)}
						
						{/* Bot√≥n de notificaciones */}
						<button
							className="text-gray-600 hover:text-gray-900 p-1 rounded-full relative transition-colors"
							onClick={toggleNotificationModal}
						>
							<Bell size={20} />
							{finalUnreadNotifications > 0 && (
								<span className="absolute top-0 right-0 transform translate-x-1/2 -translate-y-1/2 bg-red-500 text-white text-xs rounded-full w-5 h-5 flex items-center justify-center">
									{finalUnreadNotifications > 9
										? "9+"
										: finalUnreadNotifications}
								</span>
							)}
							{/* Indicador de loading para contadores */}
							{countersLoading && (
								<span className="absolute top-0 right-0 transform translate-x-1/2 -translate-y-1/2 bg-blue-500 text-white text-xs rounded-full w-5 h-5 flex items-center justify-center">
									<div className="w-2 h-2 bg-white rounded-full animate-pulse"></div>
								</span>
							)}
						</button>
					</div>

					{/* User Profile */}
					<div className="relative">
						<button
							className="profile-button flex items-center space-x-2 text-gray-800 focus:outline-none"
							onClick={toggleProfileMenu}
						>
							<div
								className={`h-8 w-8 rounded-full ${getAvatarColor()} flex items-center justify-center font-medium`}
							>
								{getInitial()}
							</div>
							<span className="hidden md:block font-medium">
								{user?.name || (isAdmin ? "Admin" : "Usuario")}
							</span>
							<ChevronDown size={18} className="hidden md:block" />
						</button>

						{/* User Dropdown Menu */}
						{isProfileMenuOpen && (
							<div className="profile-menu absolute right-0 mt-2 w-48 bg-white rounded-lg shadow-lg py-2 z-30 border border-gray-200">
								<div className="px-4 py-3 border-b border-gray-200">
									<p className="text-sm font-medium text-gray-800">
										{user?.name || (isAdmin ? "Admin" : "Usuario")}
									</p>
									<p className="text-xs text-gray-500 truncate">
										{user?.email ||
											(isAdmin ? "admin@ejemplo.com" : "usuario@ejemplo.com")}
									</p>
								</div>

								<Link
									to={isAdmin ? "/admin/profile" : "/seller/profile"}
									className="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100"
									onClick={() => setIsProfileMenuOpen(false)}
								>
									<div className="flex items-center">
										<User size={16} className="mr-2" />
										Mi Perfil
									</div>
								</Link>

								<Link
									to={isAdmin ? "/admin/settings" : "/seller/settings"}
									className="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100"
									onClick={() => setIsProfileMenuOpen(false)}
								>
									<div className="flex items-center">
										<Settings size={16} className="mr-2" />
										Configuraci√≥n
									</div>
								</Link>

								<button
									onClick={logout}
									className="block w-full text-left px-4 py-2 text-sm text-red-600 hover:bg-gray-100"
								>
									<div className="flex items-center">
										<LogOut size={16} className="mr-2" />
										Cerrar Sesi√≥n
									</div>
								</button>
							</div>
						)}
					</div>
				</div>
			</div>

			{/* ‚úÖ MODAL DE NOTIFICACIONES */}
			<NotificationModal
				isOpen={isNotificationModalOpen}
				onClose={() => setIsNotificationModalOpen(false)}
				isAdmin={isAdmin}
			/>
		</header>
	);
};

export default DashboardHeader;
